html,body, .App, .App>div {
    margin: 0;
    height: 100;
    width: 100%;
    min-height: 100vh;
    min-width: 100vw;
}

.page{
    width: 100%;
    height: 100%;
}

.midnightblue{
    background-color: #37133d;
}

.uigreen{
  color: #02b302;
}

.product{
  display: flex;
  border-bottom: 1px solid whitesmoke;

}
.product-image{
  width: 50%;
}

.product-image img{
  width: 90%;
  height: 90%;

}

.price{
  font-weight: bold;
  font-size: 1.5rem;
}

.product-details{
  display: flex;
  flex-direction: column;
}

.sandybrown{
  color: #f8bb50;
}

.flexy-center{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.options{
  display: flex;
  width: 100%;
  justify-content: space-between;
}